html,
body {
  height: 100%; }

body {
  display: flex;
  font-family: 'Amatic SC', sans-serif;
  font-size: 24px;
  line-height: 150%;
  margin: 0;
  text-align: center; }

.main-wrapper {
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-grow: 1;
  padding: 75px 0; }

.page-wrapper {
  align-items: center;
  display: flex;
  flex: 1 0 100%;
  justify-content: center; }

.button--ghost {
  background-color: transparent;
  border: 0;
  cursor: pointer;
  font-size: 44px;
  margin-top: 10px;
  padding: 10px 15px;
  text-transform: uppercase;
  width: 100px; }
  .button--ghost::before {
    color: #cccccc; }
  .button--ghost:hover::before {
    color: gainsboro; }

h1,
h2,
h3 {
  font-weight: 400; }

h2 {
  cursor: pointer;
  font-size: 24px;
  line-height: 135%;
  margin: 10px 0; }

.playfair {
  font-family: 'Playfair Display', serif;
  font-size: 20px; }

.code {
  font-family: 'VT323', sans-serif; }

.amatic {
  font-family: 'Amatic SC', serif; }

.voltaire {
  font-family: 'Voltaire', serif; }

.satisfy {
  font-family: 'Satisfy', serif; }

.breathe {
  animation: breathe 3s infinite;
  line-height: 52px; }

@keyframes breathe {
  0%,
  20% {
    font-size: 48px; }
  70% {
    font-size: 50px; }
  100% {
    font-size: 48px; } }

[data-animate='fade-in-right'] {
  opacity: 0;
  transform: translateX(20px);
  transition: all 0.5s linear; }
  [data-animate='fade-in-right'].fade-in {
    opacity: 1;
    transform: translateX(0); }
  [data-animate='fade-in-right'].fade-out {
    opacity: 0;
    transform: translateX(-20px); }

