.trophy-case {
  align-items: center;
  display: flex;
  justify-content: center;
  opacity: 0.5;
  padding-bottom: 15px;
  position: fixed;
  top: 0;
  transition: all 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
  transform: translateY(calc(-100% + 25px));
  width: 100%; }
  .trophy-case:hover {
    opacity: 1;
    transform: translateY(0); }

.trophy-case__item {
  display: none;
  justify-content: center;
  max-width: 0;
  opacity: 0.9;
  overflow: hidden;
  padding: 15px;
  text-decoration: none;
  transition: all 0.5s ease-out;
  width: 10%; }
  .trophy-case__item--polished {
    cursor: pointer;
    display: flex;
    max-width: 100px; }
    .trophy-case__item--polished:hover {
      opacity: 1; }
  .trophy-case__item::before {
    font-size: 32px; }

.font-selection__intro {
  border: 1px solid #f5f5f5;
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 1;
  margin-bottom: 30px;
  padding: 10px 15px;
  text-transform: uppercase; }

.font-selection__item {
  background-color: #161616;
  color: #c5c5c5;
  cursor: pointer;
  padding: 0 20px;
  position: relative;
  transition: all 0.5s linear; }
  .font-selection__item::before, .font-selection__item::after {
    border: 5px solid #f2c341;
    border-radius: 5px;
    content: '';
    display: block;
    height: 10px;
    opacity: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: 0.3s all linear;
    width: 10px; }
  .font-selection__item::before {
    left: 0; }
  .font-selection__item::after {
    right: 0; }
  .font-selection__item:hover {
    color: #f5f5f5; }
  .font-selection__item[data-active='true'] {
    color: #f5f5f5; }
    .font-selection__item[data-active='true']::before, .font-selection__item[data-active='true']::after {
      opacity: 1; }

.footer-menu {
  background: #f5f5f5;
  border-top: 1px solid #333333;
  bottom: 0;
  color: #333333;
  position: fixed;
  width: 100%;
  left: 0;
  right: 0; }
  .footer-menu small {
    display: block;
    font-size: 12px; }

.storybook {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0 auto;
  max-width: 675px;
  text-align: justify;
  width: 80%; }
  .storybook .storybook__container {
    max-height: 100%;
    overflow: auto; }
  .storybook em {
    cursor: pointer;
    font-style: normal;
    opacity: 1;
    text-decoration: underline; }
  .storybook .exhausted {
    cursor: default;
    font-style: italic;
    text-decoration: none; }
  .storybook .highlight {
    opacity: 0.8;
    transition: background 0.4s linear, color 0.4s linear, opacity 0.2s linear; }
    .storybook .highlight.revealed {
      background: transparent;
      opacity: 1; }
  .storybook.three::first-letter {
    font-size: 64px; }

.flex {
  display: flex !important; }

.flex--col {
  flex-direction: column; }

.hidden {
  display: none !important;
  opacity: 0.7; }

.selected {
  opacity: 1; }

.pointed {
